@font-face {
    font-family: 'RoadUI-SemiBold';
    src: url('../assets/fonts/RoadUI-SemiBold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Montserrat";
    src: url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'RoadUI-Regular';
    src: url('../assets/fonts/RoadUI-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

body {
    margin: 0;
    font-family: 'RoadUI-Regular', sans-serif;
    overflow-x: hidden;
}

.Montserrat48px {
    font-size: 48px;
    line-height: 57.6px;
    font-family: "Montserrat", sans-serif;
}

.RoadUi18px {
    font-size: 18px;
    line-height: 25.2px;
    font-family: 'RoadUI-Regular', sans-serif;
}

.container {
    width: 1240px;
    margin: 0 auto;
}

.text-active {
    color: #fbb216;
}

.button {
    padding: 12px 48px;
    text-align: center;
    border-radius: 12px;
    font-weight: 700;
    cursor: pointer;
    box-sizing: border-box;
}

.button-opacity {
    border: 2px solid rgba(251, 178, 22, 1);
}

.button-primary {
    color: #0a0a0a;
    background-color: #fbb216;
}


