/*! HOME-IMG !*/

@font-face {
    font-family: "Montserrat";
    src: url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap') format('truetype');
    font-weight: normal;
    font-style: normal;
}

main #home-page-img {
    border-radius: 24px;
    margin-bottom: 100px;
    position: relative;
}

main .text-conteiner {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

main .overlay-text {
    background-color: #00000033;
    border-radius: 24px;
    position: absolute;
    bottom: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    backdrop-filter: blur(10px);
}

.text,
.text1,
.text2 {
    margin: 16px 0px;
    color: #fffdfd;
    margin: 0;
    text-align: center;
}

.text1 {
    max-width: 778px;
    font-size: 64px;
    margin: 4px 88px 12px 88px;
}

.text {
    margin-top: 34.5px;
    white-space: nowrap;
    display: inline;
    font-size: 24px;
}

.text2 {
    max-width: 603px;
    font-size: 22px;
    text-align: center;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! SLIDER !*/

main .slider-text {
    text-align: center;
    margin-bottom: 26px;
    text-transform: uppercase;
    font-size: 28px;
    line-height: 33.6px;
}

main .about-us-container {
    display: flex;
}

main .imageGrid {
    display: flex;
    overflow: hidden;
    max-width: 2000px;
}

main .imageSet {
    display: flex;
    animation: scrollImages 40s linear infinite;
}

main .mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

@keyframes scrollImages {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

main .mainImg {
    width: 133px;
    height: 92px;
    margin-right: 40px;
    object-fit: contain;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! VIDEO !*/

.about-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 78px;
    margin-top: 178px;
}

iframe {
    width: 610px;
    height: 395px;
    border-radius: 24px;
    margin: 0;
}

.text1-container {
    height: auto;
    max-width: 552px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! SOME NUM !*/

.statisticsDatacon {
    margin-top: 107px;
}

.statisticsData {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0px;
    gap: 20px;
    width: 235px;
    height: 224px;
    background: #eb8724;
    border-radius: 32px;
    padding: 30px;
}

.statisticsData h3 {
    margin: 0 auto;
    width: 235px;
    height: 96px;
    font-family: 'RoadUI-Regular', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 80px;
    line-height: 96px;
    display: flex;
    align-items: center;
}

.statisticsData p {
    margin: 0 auto;
    width: 235px;
    height: 93px;
    font-family: 'RoadUI-Regular', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 31px;
    display: flex;
    align-items: center;
    flex: none;
    order: 1;
    align-self: stretch;
    flex-grow: 0;
}

.statistics {
    max-width: 1240px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 20px;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/


/*! services !*/

.services-container {
    display: flex;
    gap: 64px;
    margin-top: 109px;
}

.services-container.normal {
    flex-direction: row;
}

.services-container.reversed {
    flex-direction: row-reverse;
}

.mainCon {
    background-color: rgba(39, 46, 54, 1);
    border-radius: 32px;
    padding: 50px;
}

.inf-con {
    max-width: 560px;
}


/*! ---------------------------------------------------------------------------------------------------------------------------- !*/